import {
  Cog6ToothIcon,
  Cog8ToothIcon,
  ExclamationCircleIcon,
  PhotoIcon,
  SparklesIcon,
} from "@heroicons/react/24/outline";
import { Radio, Tooltip } from "antd";
import { Script } from "gatsby";
import * as React from "react";
import { LaunchButton } from "../../atoms";
import { IGeneratorConfig } from "../../types";
import { getLocalStorage, loadJSONData } from "../../utils";
import GeneratorControlsView from "./controls";
import GalleryView from "./gallery";
import SamplesView from "./samples";
import UploadView from "./upload";

var JSZip = require("jszip");

const GenerateView = ({ data }: any) => {
  const modes = ["prompt", "image"];
  const errorMsg = {
    status: false,
    status_message:
      "Error fetching generation. There is a chance the server is down. Visit the peacasso repo to learn more about running your own server",
  };
  const zip = new JSZip();

  const initConfig: IGeneratorConfig = getLocalStorage("config") || {
    prompt:
      "portrait of leprechaun, intricate, elegant, highly detailed, digital painting, artstation, concept art, smooth, sharp focus, illustration, art by artgerm and greg rutkowski and alphonse mucha, 8 k ",
    num_images: 6,
    guidance_scale: 7.5,
    num_inference_steps: 20,
    height: 512,
    width: 512,
    mode: "prompt",
  };
  const serverUrl = process.env.GATSBY_API_URL;
  const cudaUrl = `${serverUrl}/cuda`;
  const [loading, setLoading] = React.useState(false);
  const [results, setResults] = React.useState<null | {}>(null);
  const [config, setConfig] = React.useState(initConfig);
  const [error, setError] = React.useState<null | {}>(null);
  const [cudaDevices, setCudaDevices] = React.useState<null | []>(null);
  // const [uploadedImage, setUploadedImage] = React.useState<
  //   null | string | ArrayBuffer | undefined
  // >(null);

  // const updateConfig = (config: IGeneratorConfig) => {
  //   setConfig(config);
  //   setLocalStorage("config", { ...config, init_image: null });
  // };

  // React.useEffect(() => {
  //   console.log(window.LeaderLine);
  // }, []);

  // React.useEffect(() => {
  //   setConfig({ ...config, init_image: uploadedImage });
  // }, [uploadedImage]);

  const updateInitImage = (image: any) => {
    // console.log("setting init image to ", image ? image.slice(0, 100) : image);

    setConfig({
      ...config,
      mode: "image",
      init_image: image,
      mask_image: null,
    });
    // setUploadedImage(image);
  };

  React.useEffect(() => {
    if (config.mode === "prompt") {
      // setUploadedImage(null);
      setConfig({ ...config, init_image: null });
    }
  }, [config.mode]);

  // React.useEffect(() => {
  //   console.log("config change", config);
  // }, [config]);

  React.useEffect(() => {
    loadJSONData(cudaUrl)
      .then((data) => {
        setCudaDevices(data);
      })
      .catch((err) => {
        setError({ status: false, status_message: err.message });
      });
  }, []);

  const resetError = () => {
    setError(null);
  };

  // fetch generation
  const fetchGeneration = (conf: IGeneratorConfig) => {
    if (config.mode === "image" && !config.init_image) {
      setError({
        status: false,
        status_message: "Please upload an image when image mode is selected.",
      });
      return;
      // setConfig({ ...config, mode: "prompt" });
      // conf = { ...config, mode: "prompt" };
    }
    setResults(null);
    setError(null);
    const resultsUrl = `${serverUrl}/generate`;
    const nConfig = {
      ...conf,
      prompt: promptInputRef.current?.value,
    };
    setConfig(nConfig);
    // console.log(nConfig);
    const postData = {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify(nConfig),
    };
    setLoading(true);
    fetch(resultsUrl, postData)
      .then((res) => {
        const contentType = res.headers.get("content-type");
        if (res.ok) {
          if (
            contentType &&
            contentType.includes("application/x-zip-compressed")
          ) {
            res.blob().then((blob) => {
              zip
                .loadAsync(blob)
                .then(function (content: any) {
                  const fileContents: any[] = [];
                  content.forEach((filename: any) => {
                    content
                      .file(filename)
                      .async("base64")
                      .then((data: any) => {
                        fileContents.push(
                          "data:" + "image/png" + ";base64," + data
                        );
                        if (
                          fileContents.length ===
                          Object.keys(content.files).length
                        ) {
                          setLoading(false);
                          setResults({ images: fileContents, config: conf });
                        }
                      });
                  });
                })
                .catch(function (err: any) {
                  console.log(err);
                });
            });
          } else {
            setLoading(false);
            if (contentType?.includes("application/json")) {
              res.json().then((data: any) => {
                console.log("data", data);
                if (!data.status) {
                  setError(data);
                }
              });
            } else {
              res.text().then((data: any) => {
                setLoading(false);
                setError(data);
              });
            }
          }
        } else {
          setLoading(false);
          setResults(null);
          setLoading(false);
          setError(errorMsg);
        }
      })
      .catch((err) => {
        console.log("err", err);
        setResults(null);
        setLoading(false);
        setError(errorMsg);
      });
  };

  function getBase64(file: any) {
    var reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function () {
      // setUploadedImage(reader.result);
      updateInitImage(reader.result);
    };
    reader.onerror = function (error) {
      console.log("Error: ", error);
      setError({ status: false, status_message: error });
    };
  }

  const promptInputRef = React.useRef<HTMLInputElement>(null);
  const uploadInputRef = React.useRef<HTMLInputElement>(null);
  const setPromptFromSample = (prompt: string) => {
    promptInputRef.current.value = prompt;
  };

  return (
    <div className="mpb-4  pb-4">
      <Script src="https://cdn.jsdelivr.net/npm/leader-line@latest/leader-line.min.js" />
      <GeneratorControlsView
        setConfig={setConfig}
        config={config}
        generate={fetchGeneration}
        isLoading={loading}
      />
      {/* </div> */}

      <div className="  relative border border-secondary rounded p-2   mb-2">
        <div className="tex-xs -mt-5  ">
          <div className="inline-block px-2 bg-primary mb-2">
            Generation Mode
          </div>
        </div>

        <Radio.Group
          className="text-primary px-2"
          // optionType="button"
          value={config.mode}
          buttonStyle="solid"
          onChange={(e) => {
            setConfig({ ...config, mode: e.target.value });
          }}
        >
          {modes.map((mode: string, i: number) => (
            <Radio className="text-primary" key={"mode" + i} value={mode}>
              {mode}
            </Radio>
          ))}
        </Radio.Group>
        {config.mode === "prompt" && (
          <span className="text-xs text-primary  p-2 rounded">
            {/* <InformationCircleIcon className="inline-block h-4 w-4 -mt-1" />{" "} */}
            generated image is based on the text prompt.
          </span>
        )}
        {config.mode === "image" && (
          <span className="text-xs text-primary  p-2 rounded">
            generated image is based on the text prompt and uploaded image.
          </span>
        )}
      </div>
      <>
        <div
          className={`${
            loading ? " pointer-events-none" : ""
          } border border-secondary p-3 rounded gap-2 flex shadow-lg`}
        >
          <input
            className={` w-full p-3 text-gray-700 flex-1 rounded border border-secondary inline-block  ${
              loading ? "brightness-90 pointer-events-none" : ""
            }`}
            placeholder="Enter Prompt"
            type={"text"}
            defaultValue={config.prompt}
            ref={promptInputRef}
            onKeyDown={(e) => {
              if (e.key === "Enter" && promptInputRef.current && !loading) {
                // fetchGeneration(promptInputRef.current?.value);
                // fetchGeneration(promptInputRef.current?.value);
                // setPrompt(promptInputRef.current?.value);
                fetchGeneration({
                  ...config,
                  prompt: promptInputRef.current?.value,
                });
              }
            }}
          />
          {config.mode === "image" && (
            <Tooltip title="Upload an image.">
              <label htmlFor="getFile1" className="file_upload">
                <LaunchButton
                  onClick={() => {
                    console.log("click");
                    uploadInputRef.current?.click();
                  }}
                >
                  <PhotoIcon className="inline-block h-6" />
                </LaunchButton>
              </label>
            </Tooltip>
          )}
          <input
            ref={uploadInputRef}
            type="file"
            accept="image/*"
            id="getFile1"
            className="hidden"
            onChange={(event) => {
              getBase64(event.target.files[0]);
              resetError();
              uploadInputRef.current.value = "";
            }}
          />
          <LaunchButton
            onClick={() => {
              fetchGeneration({
                ...config,
                prompt: promptInputRef.current?.value,
              });
            }}
          >
            <div className="   flex relative">
              {loading && (
                <div className="inline-block   ">
                  <Cog6ToothIcon className="relative -pb-2  animate-spin  inline-flex rounded-full h-7 w-7" />
                </div>
              )}
              {!loading && (
                <span className="inline-block ">
                  {" "}
                  <SparklesIcon className="inline-block h-5 mr-2 -mt-1" />
                  {/* <Icon icon="app" size={6} /> */}
                  Generate
                </span>
              )}
            </div>{" "}
          </LaunchButton>
        </div>
      </>

      {/* <div className="h-6 w-6 relative"> */}
      <div className="flex gap-3 mt-4 mb-4">
        {config.init_image && config.mode === "image" && (
          <UploadView config={config} setConfig={setConfig} />
        )}
        <div className={`flex-1 mt-3 ${loading ? "pointer-events-none" : ""}`}>
          <SamplesView setPrompt={setPromptFromSample} />
        </div>
      </div>

      {/* </div> */}
      {loading && (
        <>
          <div className="rounded bg-secondary mt-4 p-3">
            <span className="inline-block h-6 w-6 relative mr-2">
              <Cog8ToothIcon className="animate-ping text-accent absolute inline-flex h-full w-full rounded-ful  opacity-75" />
              <Cog8ToothIcon className="relative text-accent animate-spin  inline-flex rounded-full h-6 w-6" />
            </span>
            Working .... generations may take a while, depending on the capacity
            of your GPU.
          </div>
          <div className="relative">
            <div className="loadbar rounded-b"></div>
          </div>
        </>
      )}

      {error && (
        <div className="mt-6">
          {" "}
          <div className="font-semibold  pb-1 mb-2 text-red-400">
            <ExclamationCircleIcon className="inline-block h-6 w-6 -mt-1" /> An
            error occurred. Please try again later.
          </div>
          <div className="text-secondpary border-red-400 border rounded bg-secondary p-2 px-3">
            {" "}
            {error.status_message}.
          </div>
        </div>
      )}

      {results && (
        <div className="  mt-6 rounded ">
          <GalleryView
            data={results}
            config={config}
            setInitImage={updateInitImage}
          />
        </div>
      )}
    </div>
  );
};
export default GenerateView;
